	// }
 //  },
  
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			start_date: "",
			end_date: currentDate ,
			isDis:true,
			year:'',
			month:'',
			day:'',
			countY:"",
			countM:"",
			countD:"",
			showmsg:false,
			showmsg1:false,
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		count(){
			if(this.start_date === this.end_date){
				this.showmsg = true;
				this.showmsg1=false;
			}else{
				this.showmsg = false;
				if((new Date(this.end_date)-new Date(this.start_date)) <0){
					this.showmsg1=true;
				}else{
					this.showmsg1=false;
				}
				
			}
			let date = mydiff(this.start_date, this.end_date);
			this.year=date[0];
			this.month=date[1];
			this.day=date[2];
			const date2 = dayjs(this.start_date)
			const date1 = dayjs(this.end_date)
			this.countY = date1.diff(date2, 'year',true)
			this.countM = date1.diff(date2, 'month',true)
			this.countD = date1.diff(date2, 'day')
		},
			
		bindStartDateChange: function(e) {
			
			this.start_date = e.target.value;
			this.isDis=false;
		},
		bindEndDateChange: function(e) {
			this.end_date = e.target.value;
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 100;
			} else if (type === 'end') {
				year = year + 100;
			}
			month = month > 9 ? month : '0' + month;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		}
	},
	onShow() {
		uni.showShareMenu({
			withShareTicket:true,
			title:"时间计算器",
			content:"计算时间间隔、计算年龄，快来使用吧",
			path:"pages/index/index",
			success(res){
				console.log(res);
			}
		})
	}
}
